<template>
    <mine-dialog
        title=""
        :show="show"
        @close="$emit('update:show', false)">
        <div>
            <div>
                <MinePlayground :miner="miner">
                    <span
                        :slot-scope="{ miner }"
                        slot="primary">{{ miner.primaryNum }}</span>
                    <span
                        :slot-scope="{ miner }"
                        slot="intermediate">{{ miner.intermediateNum }}</span>
                    <span
                        :slot-scope="{ miner }"
                        slot="advanced">{{ miner.advancedNum }}</span>
                    <span
                        :slot-scope="{ miner }"
                        slot="rare">{{ miner.rareNum }}</span>
                </MinePlayground>
            </div>
            <div class="record">
                <div class="coin-count"><span>{{ income.mineCurrency }}</span></div>
                <div class="coin-tip"><span>{{ totalMinerCount }}</span><span class="total">{{ income.mineCurrency }}</span></div> <!-- eslint-disable-line -->
            </div>
            <div
                @click="recordShare"
                class="share" />
        </div>
    </mine-dialog>
</template>
<script>
import {
    shareText,
} from '@/utils/jsApiV2/act';
import MineDialog from './MineDialog';
import MinePlayground from './MinePlayground';
import DialogMixin from './DialogMixin';

export default {
    name: 'MineRecordDialog',
    components: {
        MineDialog,
        MinePlayground,
    },
    mixins: [DialogMixin],
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        miner: {
            type: Object,
            default: () => ({}),
        },
        income: {
            type: Object,
            default: () => ({}),
        },
    },
    computed: {
        totalMinerCount() {
            const count = this.income.primaryNum
                + this.income.intermediateNum
                + this.income.advancedNum
                + this.income.rareNum;

            return count;
        },
    },
    methods: {
        recordShare() {
            // 
            const url = `https://feed.browser.miui.com/mobile-v2/act/invitation/share?shareId=${encodeURIComponent(this.income.invitationCode)}`;
            const text = `[]APP
[]${this.totalMinerCount}
[]${this.income.mineCurrency}
[]app
[]5
[]${this.income.invitationCode}24${url}`;

            this.$o2o({ e_c: '', e_a: '', e_n: '' });

            shareText(text, 2);
            this.$emit('update:show', false);
        },
    },
};
</script>
